import { BaseDomainEvent } from '../../common/events/DomainEvent.js';
import { Position } from '../value-objects/Position.js';
import { CellValue } from '../value-objects/CellValue.js';
import { Difficulty } from '../entities/GameState.js';

/**
 *   
 */

/**
 *   
 */
export class GameStarted extends BaseDomainEvent {
  constructor(
    gameId: string,
    difficulty: Difficulty,
    metadata: {
      gridSize: number;
      clueCount: number;
    }
  ) {
    super(
      'GameStarted',
      gameId,
      'SudokuGame',
      {
        difficulty,
        ...metadata
      }
    );
  }
}

/**
 *   
 */
export class GameCreated extends BaseDomainEvent {
  constructor(
    gameId: string,
    difficulty: Difficulty,
    seed?: number
  ) {
    super(
      'GameCreated',
      gameId,
      'SudokuGame',
      {
        difficulty,
        seed,
        gridSize: 9
      }
    );
  }
}

/**
 *   
 */
export class MoveAttempted extends BaseDomainEvent {
  constructor(
    gameId: string,
    position: Position,
    value: CellValue,
    isValid: boolean,
    conflictingPositions: Position[] = []
  ) {
    super(
      'MoveAttempted',
      gameId,
      'SudokuGame',
      {
        position: { row: position.row, col: position.col },
        value: value.value,
        isValid,
        conflictingPositions: conflictingPositions.map(pos => ({ row: pos.row, col: pos.col }))
      }
    );
  }
}

/**
 *    
 */
export class ValidMoveCompleted extends BaseDomainEvent {
  constructor(
    gameId: string,
    position: Position,
    value: CellValue,
    moveCount: number
  ) {
    super(
      'ValidMoveCompleted',
      gameId,
      'SudokuGame',
      {
        position: { row: position.row, col: position.col },
        value: value.value,
        moveCount
      }
    );
  }
}

/**
 *    
 */
export class InvalidMoveAttempted extends BaseDomainEvent {
  constructor(
    gameId: string,
    position: Position,
    value: CellValue,
    conflictingPositions: Position[],
    mistakeCount: number
  ) {
    super(
      'InvalidMoveAttempted',
      gameId,
      'SudokuGame',
      {
        position: { row: position.row, col: position.col },
        value: value.value,
        conflictingPositions: conflictingPositions.map(pos => ({ row: pos.row, col: pos.col })),
        mistakeCount
      }
    );
  }
}

/**
 *   
 */
export class LineCompleted extends BaseDomainEvent {
  constructor(
    gameId: string,
    lineType: 'row' | 'column' | 'box',
    lineIndex: number,
    completionPosition: Position
  ) {
    super(
      'LineCompleted',
      gameId,
      'SudokuGame',
      {
        lineType,
        lineIndex,
        completionPosition: { row: completionPosition.row, col: completionPosition.col }
      }
    );
  }
}

/**
 *   
 */
export class GameCompleted extends BaseDomainEvent {
  constructor(
    gameId: string,
    difficulty: Difficulty,
    finalStats: {
      elapsedTime: number;
      moveCount: number;
      mistakeCount: number;
      hintsUsed: number;
    }
  ) {
    super(
      'GameCompleted',
      gameId,
      'SudokuGame',
      {
        difficulty,
        ...finalStats,
        completedAt: new Date().toISOString()
      }
    );
  }
}

/**
 *   
 */
export class GamePaused extends BaseDomainEvent {
  constructor(
    gameId: string,
    metadata: {
      elapsedTime: number;
      moveCount: number;
    }
  ) {
    super(
      'GamePaused',
      gameId,
      'SudokuGame',
      {
        ...metadata,
        pausedAt: new Date().toISOString()
      }
    );
  }
}

/**
 *   
 */
export class GameResumed extends BaseDomainEvent {
  constructor(
    gameId: string,
    metadata: {
      elapsedTime: number;
      moveCount: number;
    }
  ) {
    super(
      'GameResumed',
      gameId,
      'SudokuGame',
      {
        ...metadata,
        resumedAt: new Date().toISOString()
      }
    );
  }
}

/**
 *   
 */
export class HintRequested extends BaseDomainEvent {
  constructor(
    gameId: string,
    position: Position,
    suggestedValue: CellValue,
    metadata: {
      hintsUsed: number;
      hintType: string;
      reasoning: string;
    }
  ) {
    super(
      'HintRequested',
      gameId,
      'SudokuGame',
      {
        position: { row: position.row, col: position.col },
        suggestedValue: suggestedValue.value,
        ...metadata
      }
    );
  }
}

/**
 *   
 */
export class HintUsed extends BaseDomainEvent {
  constructor(
    gameId: string,
    hintPosition: Position,
    suggestedValue: CellValue,
    hintType: 'simple' | 'advanced' | 'strategy',
    hintsUsedCount: number
  ) {
    super(
      'HintUsed',
      gameId,
      'SudokuGame',
      {
        hintPosition: { row: hintPosition.row, col: hintPosition.col },
        suggestedValue: suggestedValue.value,
        hintType,
        hintsUsedCount
      }
    );
  }
}

/**
 *   
 */
export class GameReset extends BaseDomainEvent {
  constructor(
    gameId: string,
    metadata: {
      previousStats: {
        moveCount: number;
        mistakeCount: number;
        hintsUsed: number;
        elapsedTime: number;
      }
    }
  ) {
    super(
      'GameReset',
      gameId,
      'SudokuGame',
      {
        ...metadata,
        resetAt: new Date().toISOString()
      }
    );
  }
}

/**
 *   
 */
export class CellSelected extends BaseDomainEvent {
  constructor(
    gameId: string,
    position: Position,
    previousPosition?: Position
  ) {
    super(
      'CellSelected',
      gameId,
      'SudokuGame',
      {
        position: { row: position.row, col: position.col },
        previousPosition: previousPosition ? { row: previousPosition.row, col: previousPosition.col } : null
      }
    );
  }
}

/**
 *   
 */
export class NumberHighlighted extends BaseDomainEvent {
  constructor(
    gameId: string,
    number: number,
    highlightedCells: Position[]
  ) {
    super(
      'NumberHighlighted',
      gameId,
      'SudokuGame',
      {
        number,
        highlightedCells: highlightedCells.map(pos => ({ row: pos.row, col: pos.col })),
        highlightCount: highlightedCells.length
      }
    );
  }
}